<template>
  <li>
    <div class="lazur-bg div">
      <i
        class="fa fa-arrow-left"
        @click="back()"
        style="text-align: right; bottom: 5px; right: 90px"
      ></i>
      <h4>{{ title }}</h4>
      <p>{{ content }}</p>
      <RouterLink :to="'/edit/' + id" class="pull-center"
        ><i class="fa fa-pencil"></i
      ></RouterLink>
    </div>
  </li>
</template>
<script>
// eslint-disable-next-line no-unused-vars
import { mapActions } from "vuex";
//import { RouterLink, RouterView } from "vue-router";
export default {
  props: {
    title: String,
    content: String,
    id: String,
  },
  methods: {
    truncateText(str, maxLength) {
      if (str.length > maxLength) {
        return str.slice(0, maxLength) + "...";
      } else {
        return str;
      }
    },
    back() {
      this.$router.push("/");
    },
    ...mapActions(["deleteNote"]),
    deleteNoteById(id) {
      this.deleteNote(id.id);
    },
    async deleteData(id) {
      try {
        const response = await fetch(
          "https://post-it-7m1i.onrender.com/notes/" + id.id,
          {
            method: "DELETE",
            headers: {
              "Content-Type": "application/json",
            },
          }
        );
        const data = await response.json();
        return data;
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>

