<template>
  <link
    href="https://maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css"
    rel="stylesheet"
  />
  <div class="container bootstrap snippets bootdey">
    <div class="row">
      <span id="post">Post </span><i class="fa fa-plus-square"></i>
      <ul class="notes detail">
        <ChildComponent
          :title="postIt.title"
          :content="content"
          :id="postIt._id"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import ChildComponent from "../components/DetailItem.vue";
export default {
  components: {
    ChildComponent,
  },
  data() {
    return { postIt: {} };
  },
  async mounted() {
    var param = this.$route.params.id;
    const url = "https://post-it-7m1i.onrender.com/notes/" + param;
    await fetch(url)
      .then((res) => res.json())
      .then((data) => {
        this.postIt = data;
        this.content = data.content[0];
      });
  },
};
</script>

